import { EventEmitter } from 'events';
import { pollRoute } from "./routes.js";
import { pollStop } from "./stops.js";
const waitFor = (delay) => new Promise(resolve => setTimeout(resolve, delay)); //Promise based setTimeout wrapper [ms]
let routesLoop;
export class Route extends EventEmitter {
    //Polls the GTT API every INTERVAL number of milliseconds, for the given ROUTE
    async poll(route, interval) {
        routesLoop = true;
        while (routesLoop) {
            await waitFor(interval);
            const data = await pollRoute(route);
            this.emit('refresh', data, route);
        }
    }
    //Stops the poll
    stop() {
        routesLoop = false;
    }
}
let stopsLoop;
export class Stop extends EventEmitter {
    constructor() { super(); }
    //Polls the GTT API every INTERVAL number of milliseconds, for the given STOP
    async poll(stop, interval) {
        stopsLoop = true;
        while (stopsLoop) {
            await waitFor(interval);
            const data = await pollStop(stop);
            this.emit('refresh', data, stop);
        }
    }
    //Stops the poll
    stop() {
        stopsLoop = false;
    }
}
